    responseSucess: async function (name, data) {
      const jsonData = {
        code: 200,
        message: "",
        name: name,
        result: data,
      };
      console.log(
        JSON.stringify(jsonData, (_, v) =>
          typeof v === "bigint" ? v.toString() : v
        )
      );
      let channelResultData = JSON.stringify(jsonData, (_, v) =>
        typeof v === "bigint" ? v.toString() : v
      );
      this.msg = channelResultData;
      // eslint-disable-next-line no-undef
      channelResultDataJS.postMessage(channelResultData);
    },

    responseError: async function (name, msg) {
      console.log(msg);
      //如果nonce 混乱,发送0余额到自己,重置nonce
      if (msg.toString().includes("is too high, valid nonce is")) {
        let nonce = msg.toString().split("is too high, valid nonce is ")[1];
        console.log("包含");
        console.log(nonce);
        let publicKey = await aeSdk.address();
        console.log(publicKey);
        await aeSdk.spend(0, publicKey, {
          denomination: AE_AMOUNT_FORMATS.AE,
          payload: "Clear Nonce last",
          nonce: nonce,
        });
      }
      const jsonData = {
        code: 500,
        name: name,
        message: msg,
      };

      let channelResultData = JSON.stringify(jsonData, (_, v) =>
        typeof v === "bigint" ? v.toString() : v
      );
      this.msg = channelResultData;
      console.log(jsonData);
      // eslint-disable-next-line no-undef
      channelResultDataJS.postMessage(channelResultData);
    },

    mapToArray(map) {
      var list = [];

      map = this.mapToObject(map);
      for (var key in map) {
        var value = map[key];
        list.push({ key: key, value: value });
      }

      return list;
    },

    mapToObject(strMap) {
      let obj = Object.create(null);
      for (let [k, v] of strMap) {
        obj[k] = v;
      }
      return obj;
    },
  },
};
